// utils/validation.ts

export const onlyLetters = (value: string) => /^[--]+$/.test(value.trim());

export const validatePassenger = (data: any) => {
    // 
    if (!data.lastName.trim()) return "   ";
    if (!onlyLetters(data.lastName)) return "    ";

    if (!data.firstName.trim()) return "   ";
    if (!onlyLetters(data.firstName)) return "    ";

    if (data.patronymic && !onlyLetters(data.patronymic))
        return "    ";

    // 
    if (!data.gender) return "  ";

    //  
    if (!data.birthday || data.birthday.length !== 10)
        return "   ";

    const [dd, mm, yyyy] = data.birthday.split("/").map(Number);

    if (dd < 1 || dd > 31) return " ";
    if (mm < 1 || mm > 12) return " ";
    if (yyyy < 1900 || yyyy > new Date().getFullYear())
        return " ";

    // 
    if (data.docType === " ") {
        if (data.docSeries.length !== 4) return "   ";
        if (data.docNumber.length !== 6) return "   ";
    }

    // 
    if (data.docType === "  ") {
        const regex = /^[IVXLCDM]+\s+[-]{2}\s+\d{6}$/;
        if (!regex.test(data.docNumber.trim())) {
            return "    . : VIII  123456";
        }
    }

    return "";
};

/*   */
export const validatePhone = (value: string) => {
    const digits = value.replace(/\D/g, "");
    return digits.length === 11 && digits.startsWith("7");
};
/*  E-mail */
export const validateEmail = (value: string) => {
    const emailRegex = /^[\w.-]+@[\w.-]+\.[A-Za-z]{2,}$/;
    return emailRegex.test(value.trim());
};

/*     */
export const validatePaymentForm = (
    personalData: {
        lastName: string;
        firstName: string;
        middleName: string;
        phone: string;
        email: string;
    },
    paymentType: "online" | "cash",
    onlineMethod: "card" | "paypal" | "qiwi" | null
) => {
    // ===  ===
    if (!personalData.lastName.trim() || !onlyLetters(personalData.lastName))
        return false;

    if (!personalData.firstName.trim() || !onlyLetters(personalData.firstName))
        return false;

    if (personalData.middleName && !onlyLetters(personalData.middleName))
        return false;

    // ===  ===
    if (!validatePhone(personalData.phone)) return false;

    // === EMAIL ===
    if (!validateEmail(personalData.email)) return false;

    // ===   ===
    if (paymentType === "cash") {
        //    ,   
        return true;
    }

    // ===  ===
    if (paymentType === "online") {
        if (!onlineMethod) return false; //      
        return true;
    }

    return false;
};